import React from "react";
import { ImSad } from "react-icons/im";
import { MdArrowBack, MdOutlineArrowForward } from "react-icons/md";
import { connect } from "react-redux";
import {
  Auth,
  DistrictList,
  FC_ChangeTransferIssue,
  FC_GetDistrictList,
  FC_GetOutGoingTransfers,
  GetTransferForUser__Interface,
  TransferStatus,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import Modal, { Themes, ModalSize } from "../../components/Modal/Modal";
import { StoreState } from "../../reducers";
import { search } from "../../utils/functions";
import TransferItem from "../RequestTransfer/TransferItem";
// import Alert, { AlertInterface } from "../../components/Alert/Alert";

// props for the component
interface AppProps {
  auth: Auth;
}

interface AppState {
  loading: boolean;
  loading_districts: boolean;
  districts: DistrictList[];
  tab: "DISTRICT-LIST" | "TRANSFER-ISSUES-LIST";
  search_district: string;
  selected_district: DistrictList | null;
  error: {
    target: null | "main-error" | "main-success";
    msg: string;
  };
  limit: number;
  page: number;
  data: GetTransferForUser__Interface[];
  total: number;
  next: number;
  previous: number;
  selected_transfer_id: string | null;
  loading_request: boolean;
  status: TransferStatus;
  transfer_update_loading: boolean;
}

let defaultError = {
  target: null,
  msg: "",
};

class _RebTransferWithIssue extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      loading_districts: false,
      districts: [],
      tab: "DISTRICT-LIST",
      search_district: "",
      selected_district: null,
      error: defaultError,
      data: [],
      limit: 10,
      page: 1,
      total: 0,
      next: 0,
      previous: 0,
      status: "ISSUED",
      selected_transfer_id: null,
      loading_request: false,
      transfer_update_loading: false,
    };
  }

  loadingData = (district: string, page: number, status?: TransferStatus) => {
    this.setState({
      loading: true,
      selected_transfer_id: null,
      data: [],
      //   transfer_issue: "",
    });
    FC_GetOutGoingTransfers(
      {
        district_code: district,
        limit: this.state.limit,
        page: page,
        status: status ? status : this.state.status,
      },
      (status_req, data, msg) => {
        this.setState({
          loading: false,
          data: data?.district_transfer_request
            ? data?.district_transfer_request
            : [],
          total: data?.total_transfer ? data.total_transfer : 0,
          next: data?.next ? +data?.next : 0,
          previous: data?.previous ? +data?.previous : 0,
          error: !status_req
            ? {
                target: "main-error",
                msg: msg,
              }
            : defaultError,
          page: page,
        });
      }
    );
  };

  changeTransferStatus = (transfer_id: string, new_status: TransferStatus) => {
    if (!transfer_id || !new_status) return;

    if (!window.confirm(`Are you sure you want to change the status?`)) return;

    this.setState({
      transfer_update_loading: true,
    });
    FC_ChangeTransferIssue(
      {
        teacher_transfer_id: transfer_id,
        transfer_issue_status: new_status,
      },
      (status, data, msg) => {
        this.setState({
          transfer_update_loading: false,
          error: status
            ? {
                target: "main-success",
                msg: "The transfer has been updated successful",
              }
            : { target: "main-error", msg: msg },
        });
      }
    );
  };

  componentDidMount(): void {
    this.setState({ loading_districts: true });
    FC_GetDistrictList((status, districts, msg) => {
      this.setState({
        loading_districts: false,
        districts: districts,
        error: status
          ? defaultError
          : {
              target: "main-error",
              msg: msg,
            },
      });
    });
  }

  selectDistrict = (district: DistrictList | null) => {
    if (this.state.transfer_update_loading === true) return;
    this.setState({ selected_district: district });
    if (district === null) return;
    this.loadingData(district.district_code, 1, this.state.status);
  };

  changeStatus = (value: TransferStatus) => {
    if (this.state.transfer_update_loading === true) return;
    if (this.state.status === value || this.state.selected_district === null)
      return;
    this.setState({
      status: value,
    });
    this.loadingData(this.state.selected_district.district_code, 1, value);
  };

  paginateData = (page: number) => {
    if (this.state.transfer_update_loading === true) return;
    if (this.state.selected_district !== null)
      this.loadingData(this.state.selected_district.district_code, page);
  };

  render() {
    let DistrictResults: DistrictList[] = search(
      this.state.districts,
      this.state.search_district,
      {
        district_code: true,
        district_name: true,
      }
    );

    let total_pages = (this.state.total / this.state.limit).toFixed(0);
    let foundTransfer = this.state.data.find(
      (transf) => transf.teacher_transfer_id === this.state.selected_transfer_id
    );

    return (
      <>
        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        {this.state.error.target === "main-error" && (
          <Alert theme="danger" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        {this.state.selected_transfer_id !== null &&
          foundTransfer !== undefined && (
            <Modal
              backDrop={true}
              theme={Themes.default}
              close={() =>
                this.setState({
                  selected_transfer_id: null,
                })
              }
              backDropClose={false}
              widthSizeClass={ModalSize.extraLarge}
              displayClose={true}
              title="Transfer details"
              padding={{
                title: true,
                body: false,
                footer: undefined,
              }}
            >
              <div className="border-t">
                <div className="p-4 text-gray-500">
                  <section>
                    Transfer id:{" "}
                    <b className="text-black">
                      {foundTransfer.teacher_transfer_id}
                    </b>
                  </section>
                  <section>
                    Teacher names:{" "}
                    <b className="text-black">
                      {foundTransfer.teacher_full_name}
                    </b>
                  </section>
                  <section>
                    Teacher's Phone number:{" "}
                    <b className="text-black">
                      {foundTransfer.teacher_phone_number}
                    </b>
                  </section>
                </div>
                <TransferItem expand={true} transf={foundTransfer} />

                <div className="pt-3 px-2 bg-gray-100 m-2">
                  {foundTransfer.transfer_issue_comment && (
                    <div className="p-2 rounded bg-gray-100">
                      {foundTransfer.transfer_issue_comment}
                    </div>
                  )}
                </div>

                {this.state.selected_transfer_id && (
                  <div className="p-4 text-right  gap-2">
                    <Button
                      theme="danger"
                      onClick={this.changeTransferStatus.bind(
                        this,
                        this.state.selected_transfer_id,
                        "ISSUE-APPROVED"
                      )}
                      loading={this.state.transfer_update_loading}
                    >
                      ISSUE-APPROVED
                    </Button>{" "}
                    <Button
                      theme="success"
                      onClick={this.changeTransferStatus.bind(
                        this,
                        this.state.selected_transfer_id,
                        "NO-ISSUE"
                      )}
                      loading={this.state.transfer_update_loading}
                    >
                      NO-ISSUE
                    </Button>
                  </div>
                )}
              </div>
            </Modal>
          )}

        <DashboardContainer>
          <div className="flex items-center">
            {this.state.selected_district !== null && (
              <section className="pr-2">
                <Button
                  theme="default"
                  onClick={this.selectDistrict.bind(this, null)}
                >
                  <MdArrowBack />
                </Button>
              </section>
            )}
            <section className="flex-1">
              <h1 className="text-2xl font-bold">Transfer with issues</h1>
              {this.state.selected_district === null ? (
                <p className="p-0 text-gray-500">Transfers reported by DDE's</p>
              ) : (
                <p className="m-0 font-bold text-accent-900">
                  {this.state.selected_district.district_name} district
                </p>
              )}
            </section>

            <div className="">
              {this.state.selected_district !== null && (
                <div className="flex flex-1 gap-1 text-right ">
                  <Button
                    size="sm"
                    theme={
                      this.state.status === "NO-ISSUE"
                        ? "success"
                        : "success-light"
                    }
                    onClick={this.changeStatus.bind(this, "NO-ISSUE")}
                  >
                    NO-ISSUE
                  </Button>
                  <Button
                    size="sm"
                    theme={
                      this.state.status === "ISSUE-APPROVED"
                        ? "danger"
                        : "danger-light"
                    }
                    onClick={this.changeStatus.bind(this, "ISSUE-APPROVED")}
                  >
                    ISSUE-APPROVED
                  </Button>
                  <Button
                    size="sm"
                    theme={
                      this.state.status === "ISSUED" ? "accent" : "accent-light"
                    }
                    onClick={this.changeStatus.bind(this, "ISSUED")}
                  >
                    ISSUED
                  </Button>
                </div>
              )}
            </div>
          </div>
        </DashboardContainer>

        {this.state.selected_district === null ? (
          <>
            <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
              <div>
                <div className="p-2">
                  <TextInput
                    error=""
                    label=""
                    name=""
                    onChange={(e) =>
                      this.setState({ search_district: e.target.value })
                    }
                    placeholder="Search district by name"
                    value={this.state.search_district}
                  />
                </div>

                {this.state.loading_districts === true ? (
                  <div className="py-8 text-center text-8xl text-accent-900">
                    <Loading className="mx-auto" />
                  </div>
                ) : this.state.districts.length >= 1 ? (
                  <>
                    {DistrictResults.length <= 0 ? (
                      <div className="py-4 text-center">
                        <h2 className="text-lg">No results found!</h2>
                        <p className="text-gray-500">
                          Please use a correct district name
                        </p>
                      </div>
                    ) : (
                      <div className="grid lg:grid-cols-2 gap-2 ">
                        {DistrictResults.map((dist) => (
                          <div
                            onClick={this.selectDistrict.bind(this, dist)}
                            key={dist.district_code}
                            className="p-2 flex items-center cursor-pointer rounded border  px-3 hover:text-yellow-600 hover:border-yellow-600 hover:bg-yellow-50 bg-white text-lg"
                          >
                            <h4 className="flex-1 text-lg">
                              {" "}
                              <b>{dist.district_name}</b> district
                            </h4>

                            {/* <span className="bg-yellow-100 text-yellow-700 font-bold py-0.5 inline-block text-sm px-1.5 rounded-full">
                              {dist.total_vacant}
                            </span> */}
                            {/* {dist.total_vacant >= 1 && ( */}
                            <button className="">
                              <MdOutlineArrowForward />
                            </button>
                            {/* )} */}
                          </div>
                        ))}
                      </div>
                    )}
                  </>
                ) : (
                  <div className="py-4 text-center">
                    <h2 className="text-8xl text-gray-400 text-center">
                      <ImSad className="inline" />
                    </h2>
                    <h2 className="text-center p-4 text-gray-400 font-bold text-2xl">
                      <p>No results found on</p>
                    </h2>{" "}
                  </div>
                )}
              </div>
            </DashboardContainer>
          </>
        ) : (
          <>
            <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
              <table className="w-full">
                <thead className="text-left bg-white">
                  <tr className="bg-accent-900 bg-opacity-10">
                    <th className="p-2 border-gray-300 border">#</th>
                    <th className="p-2 border-gray-300 border">Names</th>
                    <th className="p-2 border-gray-300 border">School Code</th>
                    <th className="p-2 border-gray-300 border">School name</th>
                    <th className="p-2 border-gray-300 border">
                      Position code
                    </th>
                    <th className="p-2 border-gray-300 border">
                      Position name
                    </th>
                    <th className="p-2 border-gray-300 border">
                      Qualification
                    </th>
                    <th className="p-2 border-gray-300 border">Error</th>
                  </tr>
                </thead>
                <tbody>
                  {this.state.loading === true ? (
                    <tr className=" ">
                      <td colSpan={8} className="py-4 text-center border">
                        <Loading />
                      </td>
                    </tr>
                  ) : this.state.data.length <= 0 ? (
                    <tr>
                      <td colSpan={8} className="py-4 text-center border">
                        <h1 className="text-2xl text-gray-400">
                          No data found
                        </h1>
                        {/* <p className="text-gray"></p> */}
                      </td>
                    </tr>
                  ) : (
                    this.state.data.map((itm, i) => (
                      <tr
                        key={i}
                        className="bg-white cursor-pointer hover:bg-yellow-400 hover:bg-opacity-30"
                        onClick={() => {
                          if (this.state.transfer_update_loading === true)
                            return;
                          this.setState({
                            selected_transfer_id: itm.teacher_transfer_id,
                          });
                        }}
                      >
                        <td className="border p-2">{i + 1}</td>
                        <td className="p-2 border">{itm.teacher_full_name}</td>
                        <td className="p-2 border">
                          {itm.school_from_info?.school_code}
                        </td>
                        <td className="p-2 border">
                          {itm.school_from_info?.school_name}
                        </td>
                        <td className="p-2 border">{itm.position_code}</td>
                        <td className="p-2 border text-sm">
                          {itm.position_name}
                        </td>
                        <td className="p-2 border">{itm.qualification_name}</td>
                        <td className="p-2 border">
                          {itm.transfer_issue_comment &&
                          itm.transfer_issue_comment !== null ? (
                            <span className="text-sm p-0.5 bg-red-400 px-2 rounded-full font-bold text-white">
                              Issue
                            </span>
                          ) : (
                            <span></span>
                          )}
                          {/* {  transfer_issue_comment?: string;
  transfer_issued_date?: string;} */}
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>

              <div className="flex gap-3 mx-auto pt-3">
                {Array(+total_pages)
                  .fill("0")
                  .map((e, i) => (
                    <button
                      className={`p-2 py-1 ${
                        +this.state.page === i + 1
                          ? "bg-accent-800 text-black font-bold"
                          : "bg-opacity-25"
                      } rounded-full bg-accent-800 `}
                      onClick={this.paginateData.bind(this, i + 1)}
                    >
                      {i + 1}
                    </button>
                  ))}
              </div>
            </DashboardContainer>
          </>
        )}
      </>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const RebTransferWithIssue = connect(
  mapStateToProps,
  {}
)(_RebTransferWithIssue);
